* {
  box-sizing: border-box;
}

body {
  background-color: #282c34;
  color: rgba(248, 217, 240, 0.77);
  background-image: -webkit-linear-gradient(top left, rgba(35, 2, 63, 0.86), rgba(203, 78, 126, 0.87));
  font-family: cursive, sans-serif;
}

#title {
  font-size: 26px;
  font-family: "Monotype Corsiva", sans-serif;
}

#second-title {
  font-size: 20px;
  font-family: "Monotype Corsiva", sans-serif;
}

#root {
  display: block;
  width: 80%;
  height: 1115px;
  margin: auto;
}

#counter {
  display: block;
  margin: auto;
  text-align: center;
}

#counter-block {
  display: flex;
  flex-direction: column;
  width: 30%;
  margin: auto;
}

#counter-block * {
  width: 100%;
  text-align: center;
}

#button-box>button{
  width: 55%;
  border-radius: 0;
  background-color: rgba(217, 84, 170, 0.87);
  color:cornsilk;
  padding: 2px;
  font-family: cursive, sans-serif;
}

#button-box>button:hover {
  background-color: rgba(252, 82, 249, 0.38);
  color:aliceblue;
}

::-webkit-scrollbar {
  width: 12px; /* width of the entire scrollbar */
}

::-webkit-scrollbar-track {
  background: rgba(120, 142, 164, 0); /* color of the tracking area */
}

::-webkit-scrollbar-thumb {
  background-color: #f692bf;
  background-image: -webkit-linear-gradient(top left, rgba(45, 0, 61, 0.86), rgba(203, 78, 126, 0.87)); /* color of the scroll thumb */
  border-radius: 10px; /* roundness of the scroll thumb */
  border: 1px solid rgba(59, 11, 61, 0.55); /* creates padding around scroll thumb */
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}